const components = [															// 
	require('&/basic/button').default,
	require('&/basic/card').default,
];

const install = function (Vue) {												//  install  Vue 
	if (install.installed) return												// 
	install.installed = true
	
	components.map(component => Vue.component(component.name, component))		// 
	// components.map(component => Vue.use(component))							// 
}

if (typeof window !== 'undefined' && window.Vue) {								//  Vue  Vue 
	install(window.Vue)
}

export default {
	install,
	//  install Vue.use()
	...components
}